.blogContainer {
  margin-top: 81px;

  @media screen and (max-width: 767px) {
    margin-top: 0;
  }
}

.blogContent {
  max-width: 1200px;
  padding: 15px;
  width: 100%;
  margin: auto;
  margin-top: 100px;
  margin-bottom: 100px;

  @media screen and (max-width: 767px) {
    margin-top: 60px;
  }

  h1 {
    font-weight: 800;
    font-size: 40px;
    line-height: 49px;
    color: #3A8195;

    @media screen and (max-width: 575px) {
      font-size: 28px;
      line-height: 2rem;
    }
  }

  h2 {
    font-weight: 600;
    font-size: 24px;
    line-height: 24px;
    color: #374A59;
    @media screen and (max-width: 575px) {
      font-size: 18px;
    }
  }

  ul {
    padding-left: 20px;
  }

  [class*="date"] {
    font-weight: 300;
    font-size: 14px;
    color: #374A59;
    margin-top: 20px;
    margin-bottom: 15px;
  }

  [class*="seperator"] {
    width: 106px;
    height: 7px;

    background: #88DBA8;
    border-radius: 3.5px;
    margin-bottom: 70px;
  }

  [class*="desc"] {
    font-size: 16px;
    font-weight: normal;
    color: #454545;
    margin-top: 40px;

    @media screen and (max-width: 575px) {
      font-size: 12px;
      margin-top: 20px;
    }
  }

  [class*="image-container"] {
    display: flex;
    justify-content: center;

    @media screen and (max-width: 575px) {
      margin-bottom: 20px;
    }
  }
}